<template>
  <div class="outBox">
    <van-nav-bar title="收货地址" left-text="返回" left-arrow @click-left="$router.go(-1)" fixed />

    <div class="addressBox">
        <van-address-list 
        v-model="aid" 
        :list="list" 
        default-tag-text="默认" 
        @add="AddNewAddress"
        @edit="AddNewAddress"
        />
    </div>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      aid: '1',
      list: [],
      
    };
  },
  created() {
    this.findAddress();
  },
  methods: {
    //查询地址
    findAddress() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          
          if (result.data.code == 20000) {
            result.data.result.forEach(v => {
             let currentAddress = {
              id: v.aid,
              tel: v.tel,
              name: v.name,
              address: v.province + v.city + v.county + v.addressDetail,
              isDefault: Boolean(v.isDefault)
            };

              //默认选择默认地址
            if (v.isDefault) {
              this.aid = v.aid;
            }
            this.list.push(currentAddress);
            });
            
            
          }

          this.$toast.clear();
        })
        .catch(() => {
          
          this.$toast.clear();
        });
    },

    //增加新地址&编辑地址
    AddNewAddress(item){
        
        
        let o = {
            name:'address'
        }
        if(item){
            o.params = {
                aid : item.id
            }
        }
        this.$router.push(o)
    }

  }
};
</script>

<style lang="less" scoped>
@c: #f80;
.outBox {
  /deep/ .van-icon-arrow-left,
  /deep/ .van-nav-bar__text {
    color: @c;
  }
  //默认地址icon颜色
  /deep/ .van-tag--danger {
    background-color: @c;
  }
  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: @c;
    border-color: @c;
  }
  /deep/ .van-button--danger {
    background-color: @c;
    border: 1px solid @c;
  }

  .addressBox{padding-top: 46px;}
}
</style>